/* import styled from '@emotion/styled'; */

/* ! 1.ОБЩИЕ Стили для устройств с шириной экрана от 0 до 479px */
    .headerShops {
        font-size: 20px;
        text-align: center;
        margin: 0;
        margin-top: 10px;
    }

    .list {
        flex-grow: 1;
        display: grid;
            /* ! Задаем 2 столбца равной ширины */
            grid-template-columns: 1fr 1fr;
            justify-items:center;
            /* align-items:center; */
            grid-gap: 7%;
            /* gap: 7%; */
        margin: 0;
        padding: 10px;
        list-style: none;
    }

    /* .listItem {
        margin-left: auto;
        margin-right: auto;
    } */

    .selectShopButton {
        min-width: 120px;
        text-decoration: none;
        padding: 8px 10px;
        font-weight: 700;
        cursor: pointer;
        border: 1px solid rgb(120, 196, 130);
        border-radius: 4px;
        color: #16739b;
        box-shadow: 12px 12px 18px rgba(59, 43, 14, 0.23);
    }
    .selectShopButton:hover,
    .selectShopButton:focus {
        background-color: #fffbcf;
        color: #9b1616;
    }


/* ! 2.Стили для устройств с шириной экрана от 480px до 767px */
@media screen and (min-width: 480px) and (max-width: 767px) {
    .list {
        /* ! Задаем 3 столбца равной ширины */
        grid-template-columns: 1fr 1fr 1fr;
        grid-gap: 2%;
        /* gap: 2%; */
    }
}


/* ! 3.Стили для устройств с шириной экрана от 768px и и выше */
@media screen and (min-width: 768px) {
    .list {
        display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            align-items: center;
        /* gap: 40px; */
    }
}


